import React, { Component } from "react";

import { Navbar, Nav } from "react-bootstrap";
//import { NavDropdown } from "react-bootstrap";
//import NavbarToggle from "react-bootstrap/esm/NavbarToggle";

import { Button } from "react-bootstrap";

//import { NavLink } from "react-router-dom";
import { Link } from "react-router-dom";

//import { NavDropdown } from "react-bootstrap";
import { ButtonGroup, DropdownButton, Dropdown } from "react-bootstrap";

import { FE_APP_VERSION } from "../../util/Constant";
//!? import params from "../../public/data/params.json";

//import { setLinksNEXTForBreadcrumbOnCACHE } from "../../action/CachedDataAction";

//import { fetchCurrentUser } from "../../action/UserAction";

//import * as toast from "./Toaster";

//import toastErrorFix from "../../util/ToastError";

//import { HOME, HOME_INDEX, TABLELIST, COLUMNLIST, INDEXLIST } from "../../util/Config";

import { HOME, HOME_INDEX, USERGUIDE, DOWNLOAD, JAVA, ANDROID, CONTACT } from "../../util/Config";

import { debugEnabled, consoleLogPrefixMsg } from "../../util/Config";

const consoleLogMsg = consoleLogPrefixMsg("! NavBarTop");
const consoleLog = (msg) => consoleLogMsg(msg);


class NavBarTop extends Component {

    constructor(props, context) {
        super(props, context);

        this.NAV_TOP_HEIGHT = null; //Unknown at first!
        this.SCROLLPOS_YPREV = 0;

        this._canMount = true; //TODO? Necessity!
        if (debugEnabled)
            consoleLog("constructor.props:" + (props ? "+EXIST" : "!ABSENT") + ", context:" + (context ? "+EXIST" : "!ABSENT"));

        //topBarVisibleStatus; 2:ShowTop1+Top2; 1:ShowTop2 only
        this.state = {
            currentUser: null,
            //linksNEXT: null
            topBarVisibleStatus: 2
        }

        //? this.context = context;
        //+ this.transitToPageBinded = this.transitToPage.bind(this);
    }


    //TODO? Executed at first!
    componentDidMount() {
        if (debugEnabled)
            consoleLog("componentDidMount._canMount:" + this._canMount + ", SCROLLPOS_YPREV:" + this.SCROLLPOS_YPREV + ", topBarVisibleStatus:" + this.state.topBarVisibleStatus);

        if (this._canMount) {
            //USED for NavBarTop to hide or visible on scrolls!
            window.addEventListener("scroll", this.handleScroll);
            //! Link.addEventListener("click", this.linkClick);
        }
    }

    componentWillUnmount() {
        this._canMount = false;
        if (debugEnabled)
            consoleLog("componentWillUnmount_canMount:" + this._canMount);

        window.removeEventListener("scroll", this.handleScroll);
    }

    fixNAV_TOP_HEIGHT = () => {
        if (this.NAV_TOP_HEIGHT == null) {
            this.NAV_TOP_HEIGHT = this.outerHeight(document.getElementById("divNavTop"));
        }

        return this.NAV_TOP_HEIGHT;
    }

    outerHeight = (elm) => {
        const height = elm.offsetHeight; //110
        const style = getComputedStyle(elm);

        const marginTopBottom = parseInt(style.marginTop) + parseInt(style.marginBottom); //0
        const ohTotal = (height + marginTopBottom);
        if (debugEnabled)
            consoleLog("outerHeight; height:" + height + ", marginTopBottom:" + marginTopBottom + ", ohTotal:" + ohTotal);

        return ohTotal; //110
    }

  // Hide or show the menu.
  handleScroll = () => {
    const scrollPosYNEW = window.pageYOffset;
    const topBarVisibleStatusPREV = this.state.topBarVisibleStatus;
    // if (debugEnabled)
    //    consoleLog("handleScroll; SCROLLPOS_YPREV:" + this.SCROLLPOS_YPREV + ", topBarVisibleStatusPREV:" + topBarVisibleStatusPREV + ", scrollPosYNEW:" + scrollPosYNEW);

    const NAV_TOP_HEIGHT = this.fixNAV_TOP_HEIGHT();
    const scrollDOWN = (scrollPosYNEW > this.SCROLLPOS_YPREV);
    let topBarVisibleStatusNEW;
    //PREV: if (scrollPosYNEW > NAV_TOP_HEIGHT) {
    if (scrollPosYNEW > ( 4 * NAV_TOP_HEIGHT)) {
        topBarVisibleStatusNEW = (scrollDOWN ? 0 : 1);
    }
    else
    //PREV: if (scrollPosYNEW > (NAV_TOP_HEIGHT / 2)) {
    if (scrollPosYNEW > NAV_TOP_HEIGHT) {
        topBarVisibleStatusNEW = (scrollDOWN ? 1 : 2);
    }
    else {
        topBarVisibleStatusNEW = 2;
    }

    // if (debugEnabled)
    //    consoleLog(">scrollDOWN:" + scrollDOWN + ", topBarVisibleStatusNEW:" + topBarVisibleStatusNEW);

    this.SCROLLPOS_YPREV = scrollPosYNEW;

    if (topBarVisibleStatusPREV === topBarVisibleStatusNEW) {
        // if (debugEnabled)
        //    consoleLog("NO_NEED to render NavBarTOP!");
        return;
    }

    this.setState({
      topBarVisibleStatus: topBarVisibleStatusNEW
    });
  };

  /*
  linkClick = (link) => {
      // if (debugEnabled)
            consoleLog("linkClick.link: " + link);

      //onClick={this.props.gotoLink}

      //window.location = link; //Forces to refresh page!
      //TypeError: Cannot read property 'push' of undefined
      this.props.history.push(link); //Goto LINK!
    }
    */

/*TODO,l?
    logout = () => {
        if (debugEnabled)
            consoleLog("logout");

        //closeUserOnSession(this.responseLogoutSuccess, this.responseLogoutError);
    }
*/

/*
    responseCurrentUser = (curUser) => {
        if (debugEnabled)
            consoleLog("responseCurrentUser._canMount:" + this._canMount + ", " + (this._canMount ? "+PROCESS" : "!SKIP"));

        if (this._canMount) {
            //TODO? Useless!
            //const linksNEXT = getLinksNEXTForBreadcrumbOnCACHE();
            //this.setState({ currentUser: curUser, linksNEXT: linksNEXT });
            this.setState({ currentUser: curUser });
        }
    }
*/


    render() {
        //const curUser = this.state.currentUser;
        //const curUserId = (curUser ? curUser.id : null);
        const topBarVisibleSTATUS = this.state.topBarVisibleStatus;
        //const divClassName  = (topBarVisibleSTATUS > 0  ? "sticky-top" : "d-none"); //PRB for scrollPosY update recursives RENDER!
        //const divClassName  = (topBarVisibleSTATUS > 0  ? "fixed-top" : "invisible");
        const nav1ClassName = (topBarVisibleSTATUS >= 2 ? "visible" : "d-none");
        const nav2ClassName = (topBarVisibleSTATUS >= 1 ? "visible" : "d-none");
        //if (debugEnabled) {
        //    consoleLog("render.topBarVisibleSTATUS:" + topBarVisibleSTATUS + ", nav1ClassName:" + nav1ClassName + ", nav2ClassName:" + nav2ClassName);
        //}

/*TODO?
.visible {
  visibility: visible !important;
}

.invisible {
  visibility: hidden !important;
}

.d-none {
  display: none !important;
}
*/
        //const navbarBgColor = { backgroundColor: "darkcyan" };
        //p1:darkslategray, //p2:darkcyan
        //const navbarBgColor = { backgroundColor: "darkcyan" };

/*TODO Link examples:
<Link to="/courses?sort=name" />
to: object
An object that can have any of the following properties:

pathname: A string representing the path to link to.
search: A string representation of query parameters.
hash: A hash to put in the URL, e.g. #a-hash.
state: State to persist to the location.
<Link
  to={{
    pathname: "/courses",
    search: "?sort=name",
    hash: "#the-hash",
    state: { fromDashboard: true }
  }}
/>

to: function
A function to which current location is passed as an argument and which should return location representation as a string or as an object

<Link to={location => ({ ...location, pathname: "/courses" })} />
<Link to={location => `${location.pathname}?sort=name`} />
*/

        const gotoLink = this.props.gotoLink;

        const styleBtn = { marginRight:"15px" }; //? height:"35px"
        const styleDdItemMargins = { marginLeft:"15px", marginTop:"5px", marginBottom:"0px", padding:"0px" }; //TODO? or use className="offset-1", offset-2

        //OK: <Navbar.Text className="text-warning", "text-light"
        /*PREV:
        <Navbar className={nav1ClassName} variant="dark" bg="info">
            <Navbar.Brand>SelimSql</Navbar.Brand>
            <Navbar.Text>Database Management System</Navbar.Text>
        </Navbar>
        */

        //TODO? table-borderless, table-bordered, .table-dark
        //<div className={nav1ClassName + " container-fluid p-1 bg-info"}>
        return (
         <div id="divNavTop" className="fixed-top">
            <div className={nav1ClassName + " container-fluid p-1 bg-info"}>
                <table className="table table-borderless">
                  <tbody>
                   <tr>
                    <td className="text-white" style={{ verticalAlign:"middle", fontSize:"2rem", width:"15%" }}>SelimSql</td>
                    <td className="text-white-50" style={{ verticalAlign:"middle", fontSize:"1.5rem" }}>Database Management System</td>
                    <td className="text-muted" style={{ verticalAlign:"middle", fontSize:"1rem", width:"10%" }}>{FE_APP_VERSION}</td>
                   </tr>
                  </tbody>
                </table>
            </div>

            <Navbar className={nav2ClassName} collapseOnSelect expand="md" variant="dark" style={{ backgroundColor: "darkcyan" }}>
              <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
              <Navbar.Collapse id="responsive-navbar-nav">
                  <Nav className="mr-auto">
                      <Link to={HOME} onClick={() => gotoLink(HOME)} style={styleBtn} alt="SelimSql">
                        <img src="/images/SSqlDbLogo36.png" className="logo-img img-circle img-responsive" alt="SSqlDbLogo36"/>
                      </Link>

                      <Link to={HOME} onClick={() => gotoLink(HOME)}><Button variant="info" style={styleBtn}>Main</Button></Link>

                      <Link to={USERGUIDE} onClick={() => gotoLink(USERGUIDE)}><Button variant="info" style={styleBtn}>User Guide</Button></Link>
                      <Link to={DOWNLOAD} onClick={() => gotoLink(DOWNLOAD)}><Button variant="info" style={styleBtn}>Download</Button></Link>

                      <Link to={JAVA} onClick={() => gotoLink(JAVA)}><Button variant="success" style={styleBtn}>Java</Button></Link>
                      <Link to={ANDROID} onClick={() => gotoLink(ANDROID)}><Button variant="success" style={styleBtn}>Android</Button></Link>

                      <Link to={CONTACT} onClick={() => gotoLink(CONTACT)}><Button variant="info" style={styleBtn}>Contact</Button></Link>

                      <DropdownButton as={ButtonGroup} title="SSql" variant="info" style={styleBtn}>
                        <p style={styleDdItemMargins}><Link to={USERGUIDE} onClick={() => gotoLink(USERGUIDE)}>User Guide</Link></p>
                        <p style={styleDdItemMargins}><Link to={DOWNLOAD} onClick={() => gotoLink(DOWNLOAD)}>Download</Link></p>
                        <Dropdown.Divider/>
                        <p style={styleDdItemMargins}><Link to={HOME_INDEX} onClick={() => gotoLink(HOME_INDEX)}>Home Index</Link></p>
                      </DropdownButton>
                  </Nav>
              </Navbar.Collapse>
            </Navbar>
          </div>);
    }//render
}

export default NavBarTop;
