import React, {Component} from "react";

import { debugEnabled, consoleLogPrefixMsg } from "../../util/Config";

const consoleLogMsg = consoleLogPrefixMsg("! SideBarRight");
const consoleLog = (msg) => consoleLogMsg(msg);

/*
<div class="container-fluid">
 <div class="row flex-xl-nowrap">
  <SideBarLeft>..
  <LinkPage>..

  <div id="sidebar-right" class="d-none d-xl-block col-xl-3" style="background-color:lightgray;">
      sidebar-right; class=d-none d-xl-block col-xl-3
  </div>
*/

class SideBarRight extends Component {

    constructor(props, context) {
        super(props, context);
        if (debugEnabled)
            consoleLog("constructor.props:" + (props ? "+EXIST" : "!ABSENT") + ", context:" + (context ? "+EXIST" : "!ABSENT"));

        /*?
        this.state = {
            show: false,
            active: getActiveMenuFromSessionStorage()
        };

        this.context = context;
        this.logout = this.logout.bind(this);
        this.closeMenu = this.closeMenu.bind(this);
        */
    }


    render() {
        if (debugEnabled)
            consoleLog("render");

        const param = this.props.param;
        const paramTitle = ( param == null ? null : param.title);
        let divContent = null;
        if (paramTitle) {
            divContent = (<span>{paramTitle}</span>);
        }

        return (
            <div style={{ paddingTop: 5 }}>
                {divContent}
            </div>);
    }
}

export default SideBarRight;
