import React, { Component } from "react";

//import { FE_APP_VERSION } from "../util/Constant";

import MainPage from "./MainPage";
import UserGuide from "./UserGuide";
import Download from "./Download";

import Java from "./Java";
import Android from "./Android";
import Contact from "./Contact";

import NotFound from "./NotFound";

import { BrowserRouter } from "react-router-dom";

import NavBarTop from "../component/Static/NavBarTop";

import SideBarLeft from "../component/Static/SideBarLeft";
import SideBarRight from "../component/Static/SideBarRight";

import NavBarBottom from "../component/Static/NavBarBottom";

//import { fetchAppVersion } from "../action/AuthAction";

import { HOME, HOME_INDEX, USERGUIDE, DOWNLOAD, JAVA, ANDROID, CONTACT } from "../util/Config";

import { debugEnabled, consoleLogPrefixMsg } from "../util/Config";

const consoleLogMsg = consoleLogPrefixMsg("! Dashboard");
const consoleLog = (msg) => consoleLogMsg(msg);

class Dashboard extends Component {

    constructor(props, context) {
        super(props, context);

        this._canMount = true;
        if (debugEnabled)
            consoleLog("constructor.props:" + (props ? "+EXIST" : "!ABSENT") + ", context:" + (context ? "+EXIST" : "!ABSENT"));

        const link = this.fixWindowLocation();
        this.state = {
            pageLink: link
        };
    
        /*TODO?
        this.state = {
            //appVersion: null
            appVersion: 1.0
        }
        */

        //? this.context = context;
    }

    fixWindowLocation = () => {
        // if (debugEnabled)
        //    consoleLog("fixWindowLocation");
    
        const wloc = (window == null ? null : window.location);
        consoleLog("wloc:" + wloc);
    
        //const ploc = props.location;
        // if (debugEnabled)
        //   consoleLog("ploc:" + ploc);
    
        const wlpathname = (wloc == null ? null : wloc.pathname);
        // if (debugEnabled)
        //   consoleLog("wlpathname:" + wlpathname);
    
        if (debugEnabled) {
            const wlsearch = (wloc == null ? null : wloc.search);
            consoleLog("wlsearch:" + wlsearch);
        }

        return wlpathname;
    }
  
/*prb!?
    gotoLink = (event) => {
      const srcElm = (event==null ? null : event.target);
      const srcTo = (srcElm == null ? null : srcElm.to);
      // if (debugEnabled)
        consoleLog("gotoLink.srcTo: " + srcTo);
        ..
    }
*/

    gotoLink = (link) => {
       if (debugEnabled)
          consoleLog("gotoLink.link: " + link);

       //TODO? After onclick prb on refresh win.loc!
       //const linkWin = this.fixWindowLocation();
       // if (debugEnabled)
       //   consoleLog("->fixWindowLocation: " + linkWin);

       //window.location = link; //force to go to link seperately!
       //this.props.history.push(link);

       this.setState({ pageLink: link });
    }


    /*TODO?
    state = {
        collapsed: false,
    };

    toggle = () => {
        const collapsedLast = this.state.collapsed;
        if (debugEnabled)
            consoleLog("toggle.collapsedPrev:" + collapsedLast);

        this.setState({
            collapsed: !collapsedLast,
        });
    }
    */


    componentDidMount() {
        if (debugEnabled)
            consoleLog("componentDidMount._canMount:" + this._canMount);

        //TODO? navigator.onLink

        //TODO?
        //if (this._canMount)
        //    fetchAppVersion(this.responseAppVersion);
    }

    componentWillUnmount() {
        this._canMount = false;
        if (debugEnabled)
            consoleLog("componentWillUnmount_canMount:" + this._canMount);
    }

    /*
    responseAppVersion = (appVer) => {
        if (debugEnabled)
            consoleLog("responseAppVersion_canMount:" + this._canMount + ", " + (this._canMount ? "+PROCESS" : "!SKIP"));
    
        if (this._canMount)
            this.setState({ appVersion: appVer });
    }
    */

//Note: Layout ref mk! (https://www.w3schools.com/bootstrap4/bootstrap_ref_all_classes.asp)
    render() {
        const pageLink = this.state.pageLink;
        if (debugEnabled)
            consoleLog("render; canMount:" + this._canMount + ", pageLink:" + pageLink);

        let GoMainPage;
        const paramSideBarLeft = {};
        const paramSideBarRight = {};

        //TODO!!! Also CARE SideBarLeft
        //TODO!!! Also CARE SideBarRight

        if (pageLink === HOME || pageLink === HOME_INDEX) {
            GoMainPage = MainPage;
            //paramSideBarLeft.main = pageLink;
            paramSideBarLeft.title = "NO_SEARCH";
            paramSideBarRight.title = "NO_ADS";
            //...
        }
        else
        //TODO? if (pageLink === USERGUIDE || pageLink === ("/#" + USERGUIDE)) {
        if (pageLink === USERGUIDE) {
            GoMainPage = UserGuide;
            //paramSideBarLeft.main = pageLink;
            paramSideBarLeft.title = "UserGuide_SEARCH";
            paramSideBarRight.title = "UserGuide_ADS";
        }
        else
        if (pageLink === DOWNLOAD) {
            GoMainPage = Download;
            paramSideBarLeft.title = "Download_SEARCH";
            paramSideBarRight.title = "Download_ADS";
        }
        else
        if (pageLink === JAVA) {
            GoMainPage = Java;
            paramSideBarLeft.title  = "JAVA_SEARCH";
            paramSideBarRight.title = "JAVA_ADS";
        }
        else
        if (pageLink === ANDROID) {
            GoMainPage = Android;
            //paramSideBarLeft.title  = null;
            //paramSideBarRight.title = null;
        }
        else
        if (pageLink === CONTACT) {
            GoMainPage = Contact;
        }
        else
        {
            GoMainPage = NotFound;
        }
      
        //TODO? row style={{ minHeight: calc(100vh - 80px)}}

        // !!! CARE !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
        // NavBarTop.outerHeight; height:148, marginTopBottom:0, ohTotal:148
        const styleMTop = { marginTop:150 }; //px
        // !!! CARE !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!

        //const styleSidesBG = { backgroundColor: "lightgoldenrodyellow" };
        const styleSidesBG = { backgroundColor: "lightgray" };
        //alternatives: lightcyan, lightgray, lightgoldenrodyellow, lightslategray, lightskyblue, lightyellow

        //TODO? role="main"

        return (
           <BrowserRouter>
            <div>
                <NavBarTop props={this.props} gotoLink={this.gotoLink}/>
                { /*styleMTop:150px USED for FIXED_NAVBAR */}

                <div className="container-fluid" style={styleMTop}>
                    <div className="row flex-xl-nowrap min-vh-100">
                        <div id="sidebar-left" className="col-12 col-md-3 col-xl-2" style={styleSidesBG}>
                            <SideBarLeft param={paramSideBarLeft}/>
                        </div>

                        <main id="main-middle" role="main" className="col-12 col-md-9 col-xl-7">
                            <GoMainPage/>
                        </main>

                        <div id="sidebar-right" className="d-none d-xl-block col-xl-3" style={styleSidesBG}>
                            <SideBarRight param={paramSideBarRight}/>
                        </div>
                    </div>
                </div>

                <NavBarBottom/>
            </div>
           </BrowserRouter>
        );
    }
}

export default Dashboard;
