import React, { Component } from "react";

import { debugEnabled, consoleLogPrefixMsg } from "../util/Config";

const consoleLogMsg = consoleLogPrefixMsg("Java");
const consoleLog = (msg) => consoleLogMsg(msg);

class Java extends Component {

  constructor(props) {
    super(props);
    if (debugEnabled)
      consoleLog("constructor");
  }

  /*?
  componentDidMount() {
    if (debugEnabled)
      consoleLog("componentDidMount");
  }
  */

  render() {
    if (debugEnabled)
      consoleLog("render");

    return (
      <div>
        <h2>Java</h2>
      </div>
    );
  }
}

export default Java;
