import React, { Component } from "react";

import { debugEnabled, consoleLogPrefixMsg } from "../util/Config";

const consoleLogMsg = consoleLogPrefixMsg("Android");
const consoleLog = (msg) => consoleLogMsg(msg);

class Android extends Component {

  constructor(props) {
    super(props);
    if (debugEnabled)
      consoleLog("constructor");
  }

  /*?
  componentDidMount() {
    if (debugEnabled)
      consoleLog("componentDidMount");
  }
  */

  render() {
    if (debugEnabled)
      consoleLog("render");

    return (
      <div>
       <h2>Android</h2>
      </div>
    );
  }
}

export default Android;
