import React, { Component } from "react";

import { debugEnabled, consoleLogPrefixMsg } from "../util/Config";

const consoleLogMsg = consoleLogPrefixMsg("Contact");
const consoleLog = (msg) => consoleLogMsg(msg);

class Contact extends Component {

  constructor(props) {
    super(props);
    if (debugEnabled)
      consoleLog("constructor");
  }

  /*?
  componentDidMount() {
    if (debugEnabled)
      consoleLog("componentDidMount");
  }
  */

  render() {
    if (debugEnabled)
      consoleLog("render");

    return (
      <div>
        <h2>SelimSql Contact</h2>
        <p>
        For your requests, questions and suggestions, send email to <a href="mailto:selimsqldb@gmail.com">selimsqldb@gmail.com</a>.
        </p>
      </div>);
  }
}

export default Contact;
