const processEnv = fixProcessEnv();
//console.log("processEnv:" + processEnv);

//export
const ENV_development = "development";
//const ENV_test = "test";
const ENV_production = "production";

function fixProcessEnv() {
    let processEnv = process.env.NODE_ENV;
    if (!processEnv)
        processEnv = ENV_development;
    return processEnv;
}


//---------------------------------
//logLEVELApp:0(NONE); 1(DEBUG+INFO); 2(INFO only)
const logLEVELApp = 1;
console.log("logLEVELApp:" + logLEVELApp + ", processEnv:" + processEnv);

/*
export const isDebugEnabled = () => {
    return logLEVELApp >= 1;
};
*/

export const debugEnabled = (logLEVELApp === 1) && (processEnv === ENV_development);
if (debugEnabled)
    console.log("debugEnabled:" + debugEnabled);

export const infoEnabled = (logLEVELApp <= 2 && logLEVELApp >= 1) && (processEnv !== ENV_production)
if (infoEnabled)
    console.log("infoEnabled:" + infoEnabled);

export const consoleLogPrefixMsg = (prefix) => (msg) => console.log(prefix + "." + msg);

//TODO?
//export const warnEnabled = (logLEVELApp <= 3 && logLEVELApp >= 1);
//console.log("warnEnabled:" + warnEnabled);

//export const errorEnabled = (logLEVELApp <= 4 && logLEVELApp >= 1);
//console.log("errorEnabled:" + errorEnabled);
//---------------------------------------------


let envApp = null;
export function fixEnv() {
    if (envApp != null)
        return envApp;

    envApp = Object.assign({}, config[processEnv]);
    if (debugEnabled)
        console.log("!! envApp.url: " + envApp.url);

    return envApp;
}


//---------------------------------
export const contextRoot = config.contextRoot;
if (infoEnabled) {
  console.log("contextRoot: " + contextRoot);
  //test?
  console.log("config.dateYMDFormat: " + config.dateYMDFormat);
  console.log("config.timeHmFormat: " + config.timeHmFormat);
}

//For caring WAR type routing main page:
export function fixContextPath(url) {
  let urlOut = url;
  if (contextRoot)
      urlOut = contextRoot + urlOut;

  if (debugEnabled)
    console.log("fixContextPath.urlOut: [" + urlOut + "]");

  return urlOut;
}


//---------------------------------
//PAGES
export const HOME = fixContextPath("/");
export const HOME_INDEX = fixContextPath("/index.html");

//export const LOGIN = fixContextPath("/login");

export const USERGUIDE = fixContextPath("/userguide");

export const DOWNLOAD = fixContextPath("/download");

export const JAVA = fixContextPath("/java");

export const ANDROID = fixContextPath("/android");

export const CONTACT = fixContextPath("/contact");
